import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Zoom from "react-reveal/Zoom";
import Slide from "react-reveal/Slide";
import Fade from "react-reveal/Fade";
import img1 from "./previewImages/1.png";
import img2 from "./previewImages/2.png";
import img3 from "./previewImages/3.png";
import img4 from "./previewImages/4.png";
import img5 from "./previewImages/5.png";
import img6 from "./previewImages/6.png";
import img7 from "./previewImages/7.png";
import img8 from "./previewImages/8.png";
import img9 from "./previewImages/9.png";
import img10 from "./previewImages/10.png";
import img11 from "./previewImages/11.png";
import img12 from "./previewImages/12.png";
import img13 from "./previewImages/13.png";
import img14 from "./previewImages/14.png";
import img15 from "./previewImages/15.png";
import img16 from "./previewImages/16.png";
import img17 from "./previewImages/17.png";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "#C7F7FA",
    [theme.breakpoints.between("xs", "sm")]: {
      height: "120vh",
      marginBottom: "-5rem",
    },
  },
  minting: {
    backgroundColor: "#EE6C4D",
    width: "100%",
    height: "70px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: "35px",
    fontFamily: "Montserrat",
    fontWeight: "700",
    color: "#C7F7FA",
    marginBottom: "1rem",
    [theme.breakpoints.between("xs", "sm")]: {
      fontSize: "1.2rem",
      fontWeight: "700",
    },
  },
  firstRow: {
    display: "flex",
    gap: "1rem",
    justifyContent: "center",
    padding: "0 2rem",
    marginBottom: ".5rem",
  },
  firstRowImages: {
    width: "280px",
    hieght: "280px",
    [theme.breakpoints.between("xs", "sm")]: {
      width: "22%",
    },
  },
  secondRow: {
    display: "flex",
    gap: "1rem",
    justifyContent: "center",
    padding: "0 2rem",
    marginBottom: ".5rem",
  },
  info: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: "20px",
    fontWeight: "900",
    padding: "5rem 0 7rem 0",
    color: "#ff6700",
    [theme.breakpoints.between("xs", "sm")]: {
      display: "none",
    },
  },
  link: {
    color: "#ff6700",
    paddingLeft: ".5rem",
  },
  phoneText: {
    display: "none",
    [theme.breakpoints.between("xs", "sm")]: {
      display: "flex",
      justifyContent: "center",

      alignItems: "center",
      textAlign: "center",
      width: "100vw",
      padding: "1.4rem 3rem",
      color: "#ff6700",
      fontWeight: "500",
      fontSize: "1.1rem",
      flexDirection: "column",
      gap: "2rem",
    },
  },
}));

export default function Preview() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Zoom>
        <div className={classes.minting}>
          Minting June 14 2022 coming soon to metaverse
        </div>
      </Zoom>
      <Slide left>
        <div className={classes.firstRow}>
          <img className={classes.firstRowImages} src={img1} alt="" />
          <img className={classes.firstRowImages} src={img2} alt="" />
          <img className={classes.firstRowImages} src={img3} alt="" />
          <img className={classes.firstRowImages} src={img4} alt="" />
          <img className={classes.firstRowImages} src={img5} alt="" />
        </div>
      </Slide>
      <Slide right>
        <div className={classes.secondRow}>
          <img className={classes.firstRowImages} src={img6} alt="" />
          <img className={classes.firstRowImages} src={img7} alt="" />
          <img className={classes.firstRowImages} src={img8} alt="" />
          <img className={classes.firstRowImages} src={img9} alt="" />
          <img className={classes.firstRowImages} src={img10} alt="" />
          <img className={classes.firstRowImages} src={img11} alt="" />
        </div>
      </Slide>

      <Slide right>
        <div className={classes.firstRow}>
          <img className={classes.firstRowImages} src={img12} alt="" />
          <img className={classes.firstRowImages} src={img13} alt="" />
          <img className={classes.firstRowImages} src={img14} alt="" />
          <img className={classes.firstRowImages} src={img15} alt="" />
          <img className={classes.firstRowImages} src={img16} alt="" />
        </div>
      </Slide>
      <div className={classes.info}>
        THESE ARE PREVIEWS, NOT ACTUAL TOKENS. MORE SNEAK PEEKS ON{" "}
        <a className={classes.link} href="">
          DISCORD
        </a>{" "}
        .
      </div>
      <div className={classes.phoneText}>
        Minecraft is an independent game that became very famous among gamers
        all over the world, and not for its graphics and impressive effects, but
        for its simple graphic engine and for the fact that everything is made
        out of cubes.
        {/* <p style={{ color: "#293241" }}>
          Find out more on{" "}
          <a className={classes.link} href="">
            DISCORD
          </a>{" "}
        </p> */}
      </div>
    </div>
  );
}
