import React, { useState } from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import { Typography } from "@mui/material";
import { makeStyles } from "@material-ui/core/styles";
import PublicIcon from "@mui/icons-material/Public";
import Stack from "@mui/material/Stack";
import InstagramIcon from "@mui/icons-material/Instagram";
import TelegramIcon from "@mui/icons-material/Telegram";
import TwitterIcon from "@mui/icons-material/Twitter";
import Button from "@mui/material/Button";
import Avatar from "@mui/material/Avatar";
import first from "./1.png";
import second from "./2.png";
import third from "./3.png";
import Carousel from "react-spring-3d-carousel";
import { v4 as uuidv4 } from "uuid";
import { config } from "react-spring";
import { createTheme } from "@mui/material/styles";
import SendIcon from "@mui/icons-material/Send";
import Fade from "react-reveal/Fade";
import Zoom from "react-reveal/Zoom";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "#293241",
    width: "100vw",
    height: "100vh",
    color: "white",
    [theme.breakpoints.between("xs", "sm")]: {
      height: "120vh",
    },
    [theme.breakpoints.between("sm", "md")]: {
      backgroundColor: "#ffff",
    },
    [theme.breakpoints.between("md", "lg")]: {
      backgroundColor: "#cccccc",
    },
    [theme.breakpoints.between("lg", "xl")]: {
      backgroundColor: "#293241",
    },
    // [theme.breakpoints.up("sm")]: {
    //   backgroundColor: "#293241",
    // },
  },
  nav: {
    width: "100vw",
    height: "10%",
    display: "flex",
    padding: "3.5rem 4rem",
    [theme.breakpoints.between("xs", "sm")]: {
      borderBottom: "3px solid #EE6C4D",
      flexDirection: "column",
      height: "30%",
      justifyContent: "center",
      alignItems: "center",
      gap: "2rem",
    },
  },
  logo: {
    // border: "1px solid purple",
    width: "30%",
    display: "flex",
    alignItems: "center",
    paddingLeft: "2rem",
    gap: "5px",

    [theme.breakpoints.between("xs", "sm")]: {
      width: "100%",
      justifyContent: "center",
      paddingLeft: "0",
    },
  },
  logoIcon: {},
  logoTitle: {
    fontFamily: "Montserrat",
    fontWeight: "500",
    letterSpacing: "1px",
    fontSize: "1.4rem",
    color: "#E0FBFC",

    [theme.breakpoints.between("xs", "sm")]: {
      fontSize: "2.5rem",
    },
  },
  icons: {
    width: "70%",
    // border: "1px solid green",
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    paddingRight: "2rem",
    gap: "8px",

    [theme.breakpoints.between("xs", "sm")]: {
      width: "100%",
      justifyContent: "center",
      paddingLeft: "0",
    },
  },
  buttonIcons: {
    display: "flex",
    gap: "4px",
    boxShadow: "none",
    background: "linear-gradient(45deg, #293241 30%, #293241 90%)",
    border: 0,
    borderRadius: "50%",
    borderRadius: "50px",
    boxShadow: "none !important",
    color: "#E0FBFC !important",
    height: "40px",
    padding: "0 30px",
    textTransform: "capitalize !important",

    [theme.breakpoints.between("xs", "sm")]: {
      padding: "0 50px !important",
      fontSize: ".8rem !important",
      borderRadius: "5px !important",
    },

    btnText: {
      fontSize: ".8rem",
      textTransform: "capitalize !important",
      fontWeight: "600",
      fontFamily: "Roboto,sans-serif",
      color: "#E0FBFC",
    },
  },

  // --------------HERO---------------------------------
  hero: {
    // border: "2px solid blue",
    width: "100vw",
    height: "90%",
    display: "flex",
    padding: "0rem  3rem",
    paddingTop: "1rem",
    justifyContent: "center",
    alignItems: "center",
    gap: "1rem",
    marginTop: "-4rem",

    [theme.breakpoints.between("xs", "sm")]: {
      flexDirection: "column-reverse",
      // border: "2px solid red",
      paddingBottom: "10rem",
      paddingLeft: "2rem",
      paddingRight: "2rem",
      marginTop: "0rem",
    },
  },
  images: {
    // border: "1px solid blue",
    width: "35%",
    height: "250px",
    margin: "0 10px",
    transform: "rotate(90deg)",

    [theme.breakpoints.between("xs", "sm")]: {
      transform: "rotate(180deg)",
      width: "90%",
      margin: "0px !important",
    },
  },
  carousel: {
    transform: "rotate(90deg)",
    [theme.breakpoints.between("xs", "sm")]: {
      padding: "4rem",
      // border: "2px solid blue !important",
    },
  },
  head: {
    width: "55%",
    display: "flex",
    flexDirection: "column",
    gap: "1.2rem",
    [theme.breakpoints.between("xs", "sm")]: {
      width: "100%",
      alignItems: "center",
      justifyContent: "center",
      textAlign: "center",
      padding: "2rem 0",
      // border: "1px solid blue",
    },
  },
  headText: {
    display: "flex",
    flexDirection: "column",
    gap: ".5rem",
    [theme.breakpoints.between("xs", "sm")]: {
      width: "100vw",
    },
  },
  header: {
    // border: "1px solid red",
    fontFamily: "Montserrat,sans-serif",
    fontSize: "55px",
    fontWeight: "900",
    color: "#EE6C4D",
    width: "100%",
    lineHeight: "1.3",
    letterSpacing: "1px",
    display: "flex",
    flexDirection: "column",

    [theme.breakpoints.between("xs", "sm")]: {
      fontSize: "2rem",
      width: "100vw",
    },
    // justifyItems: "center",
    // alignItems: "center",
  },
  info: {
    color: "#E0FBFC",
    fontSize: "1.1rem",
    fontFamily: "Roboto,sans-serif",
    lineHeight: "1.7",
    [theme.breakpoints.between("xs", "sm")]: {
      display: "none",
    },
  },
  buttons: {},
  discord: {
    color: "#EE6C4D !important",
    borderColor: "#EE6C4D !important",
    textTransform: "capitalize",
    borderRadius: "50px",
    fontSize: "1.2rem",
    border: "2px solid",
    [theme.breakpoints.between("xs", "sm")]: {
      fontSize: "1.2rem !important",
      border: "2px solid !important",
    },
  },
}));

export default function Hero() {
  const [goToSlide, setgoToSlide] = useState(0);
  const [offsetRadius, setoffsetRadius] = useState(6);
  const [showNavigation, setshowNavigation] = useState(false);
  const [value, setValue] = useState(0);

  let slides = [
    {
      key: uuidv4(),
      content: (
        <img
          style={{
            borderRadius: "100%",
            transform: "rotate(-90deg)",

            padding: "0rem",
          }}
          src={first}
          alt="1"
        />
      ),
    },
    {
      key: uuidv4(),
      content: (
        <img
          style={{
            borderRadius: "100%",
            transform: "rotate(-90deg)",
          }}
          src={second}
          alt="2"
        />
      ),
    },
    {
      key: uuidv4(),
      content: (
        <img
          style={{ borderRadius: "100%", transform: "rotate(-90deg)" }}
          src={third}
          alt="3"
        />
      ),
    },
  ].map((slide, index) => {
    return { ...slide, onClick: () => setgoToSlide(index) };
  });

  const classes = useStyles();

  return (
    <React.Fragment>
      <CssBaseline />
      <div className={classes.root}>
        <nav className={classes.nav}>
          <div className={classes.logo}>
            <PublicIcon
              style={{ fontSize: "3rem", color: "#EE6C4D" }}
              fontSize="large"
            />
            <h1 className={classes.logoTitle}>Drogirat</h1>
          </div>
          <div className={classes.icons}>
            <Button className={classes.buttonIcons} variant="contained">
              <InstagramIcon style={{ fontSize: "1.4rem", color: "#E0FBFC" }} />
              <p className={classes.btnText}>Instagram</p>
            </Button>
            <Button
              className={classes.buttonIcons}
              style={{
                borderRadius: "50px",
                boxShadow: "none",
                color: "#E0FBFC",
              }}
              variant="contained"
            >
              <TelegramIcon style={{ fontSize: "1.4rem", color: "#E0FBFC" }} />
              <p className={classes.btnText}>Telegram</p>
            </Button>
            <Button className={classes.buttonIcons} variant="contained">
              <TwitterIcon style={{ fontSize: "1.4rem", color: "#E0FBFC" }} />
              <p className={classes.btnText}>Twitter</p>
            </Button>
            <Button
              className={classes.buttonIcons}
              style={{
                borderRadius: "50px",
                background: "linear-gradient(45deg, #EE6C4D 30%, #EE6C4D 90%)",
              }}
              variant="contained"
            >
              <p className={classes.btnText}>Our RoadMap</p>
            </Button>
          </div>
        </nav>
        {/*--------------------------------------------------- HERO ------------------------------------------*/}
        <div className={classes.hero}>
          <div className={classes.head}>
            <div className={classes.headText}>
              <Fade left>
                <div className={classes.header}>
                  <span>THE SUPERDOGS </span> <span>HYPIXEL QR NFT</span>
                </div>{" "}
              </Fade>

              <div className={classes.info}>
                Minecraft is an independent game that became very famous among
                gamers all over the world, and not for its graphics and
                impressive effects, but for its simple graphic engine and for
                the fact that everything is made out of cubes.
              </div>
            </div>

            <div className={classes.buttons}>
              <Fade left>
                {" "}
                <Button
                  className={classes.discord}
                  variant="outlined"
                  endIcon={<SendIcon />}
                >
                  Join Discord
                </Button>{" "}
              </Fade>
            </div>
          </div>

          <div className={classes.images}>
            <Carousel
              className={classes.carousel}
              slides={slides}
              goToSlide={goToSlide}
              offsetRadius={offsetRadius}
              showNavigation={showNavigation}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
